import {newsDetail} from '../../service/news.js'
export default {
	data() {
		return {
			id:'',
			detail:{
				add_time:''
			}
		}
	},
	onLoad(options){
		this.id = options.id
		this.getNewsDetail()
	},
	methods: {
		async getNewsDetail(){
			const {data:res} = await newsDetail(this.id)
			this.detail = res.message[0]
		}
	}
}
